.sbx-custom {
  display: inline-block;
  position: relative;
  white-space: nowrap;
  box-sizing: border-box;
  font-size: 1em;
}

.sbx-custom__wrapper {
  width: 100%;
  height: 100%;
}

.sbx-custom__input {
  display: inline-block;
  transition: box-shadow 0.4s ease, background 0.4s ease;
  border: 0;
  border-radius: 4px !important;
  box-shadow: inset 0 0 0 2px #aaa;
  background: #fff;
  padding: 0;
  padding-right: 56px;
  padding-left: 10px;
  width: 10em;
  height: 32px;
  vertical-align: middle;
  white-space: normal;
  font-size: inherit;
  appearance: none;
}

.sbx-custom__input::-webkit-search-decoration, .sbx-custom__input::-webkit-search-cancel-button, .sbx-custom__input::-webkit-search-results-button, .sbx-custom__input::-webkit-search-results-decoration {
  display: none;
}

.sbx-custom__input:hover {
  box-shadow: inset 0 0 0 2px #919191;
}

.sbx-custom__input:focus, .sbx-custom__input:active {
  outline: 0;
  box-shadow: inset 0 0 0 2px #cb6015;
  background: #fff;
  width: 15em;
}

.sbx-custom__input::placeholder {
  color: #bbb;
}

.sbx-custom__submit,
.sbx-custom .txex-button--search {
  position: absolute;
  top: 0;
  right: 0;
  left: inherit;
  margin: 0;
  border: 0;
  border-radius: 0 3px 3px 0;
  background-color: rgba(255, 255, 255, 0);
  padding: 0;
  width: 32px;
  height: 100%;
  vertical-align: middle;
  text-align: center;
  font-size: inherit;
  user-select: none;
}

.sbx-custom__submit::before,
.sbx-custom .txex-button--search::before {
  display: inline-block;
  margin-right: -8px;
  height: 100%;
  vertical-align: middle;
  content: '';
}

.sbx-custom__submit:hover, .sbx-custom__submit:active,
.sbx-custom .txex-button--search:hover,
.sbx-custom .txex-button--search:active {
  cursor: pointer;
}

.sbx-custom__submit:focus,
.sbx-custom .txex-button--search:focus {
  outline: 0;
}

.sbx-custom__submit svg,
.sbx-custom .txex-button--search svg {
  width: 16px;
  height: 16px;
  vertical-align: middle;
  fill: #aaa;
}

.sbx-custom__reset,
.sbx-custom .txex-button--reset {
  display: none;
  position: absolute;
  top: 4px;
  right: 32px;
  margin: 0;
  border: 0;
  background: none;
  cursor: pointer;
  padding: 0;
  font-size: inherit;
  user-select: none;
  fill: rgba(0, 0, 0, 0.5);
}

.sbx-custom__reset:focus,
.sbx-custom .txex-button--reset:focus {
  outline: 0;
}

.sbx-custom__reset svg,
.sbx-custom .txex-button--reset svg {
  display: block;
  margin: 4px;
  width: 16px;
  height: 16px;
  fill: #aaa;
}

.sbx-custom__input:valid ~ .sbx-custom__reset,
.sbx-custom__input:valid ~ .txex-button--reset,
.sbx-custom__input:active ~ .txex-button--reset {
  display: block;
  animation-name: sbx-reset-in;
  animation-duration: 0.15s;
}

@keyframes sbx-reset-in {
  0% {
    transform: translate3d(-20%, 0, 0);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}

